	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/10/haybales.jpg\" border=\"0\" height=\"240\" width=\"320\" alt=\"haybales.jpg\" /></p>\n<p>Last night we hosted a <em>Laternenumzug</em> for Christopher's <em>Tagesmutter</em>, even though Christopher has now graduated to Kindergarten and no longer attends day care. We had about 16 parents and 8 children marching in the drizzle through the fields with paper lanterns. We set up hay bales and some Halloween decorations in the carport, and offered pretzels and warm drinks. The kids played in the back yard, pretending it was not dark. Most of Heidi's kids are younger than 3, so most of the guests headed home by 8 pm.  A hard core stayed a little longer.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/10/jacques.jpg\" border=\"0\" height=\"180\" width=\"135\" alt=\"jacques.jpg\" /></p>","fields":{"title":"Lantern March","date":"16 Oct 2004","url":"https://papascott.de/2004-10-16-lantern-march/"}}},"pathContext":{"slug":"/2004-10-16-lantern-march/","prev":{"fields":{"title":"Chilling out the Burger King","date":"16 Oct 2004","slug":"/2004-10-16-chilling-out-the-burger-king/"}},"next":{"fields":{"title":"Conquerors or Liberators Revisited","date":"17 Oct 2004","slug":"/2004-10-17-conquerors-or-liberators-revisited/"}}}}

